import { TokensMap } from '@/types'
import { Token } from '@cetus/types'
import { HTextLabelBox, SingleCoinImage } from '@cetus/ui-kit'
import { textEllipses } from '@cetus/utils'
import { HStack, Skeleton, Stack, Text } from '@chakra-ui/react'
import WithTooltipInfo from './WithTooltipInfo'

type RatioTypes = 'image' | 'text' | 'zap'

type DepositRatioProps = {
  type: RatioTypes
  tokenA?: Token
  tokenB?: Token
  percentMap: TokensMap
  lockRatio?: boolean
  isReverse?: boolean
  isLoading?: boolean
  label?: string
}

function DepositRatio({
  tokenA,
  tokenB,
  type = 'image',
  percentMap,
  lockRatio = false,
  isReverse = false,
  isLoading = false,
  label
}: DepositRatioProps) {
  return (
    <>
      {type === 'image' && (
        <WithTooltipInfo
          label="Deposit Ratio"
          tooltip="The ratio of two assets depends on the range you set and the current price."
          wrapStyle={{ gap: '0px' }}
        >
          <Stack flexDir={{ base: 'column', lg: 'row' }} gap={{ base: '4px', lg: '12px' }} justify={{ base: 'flex-end', lg: 'space-between' }}>
            {[tokenA, tokenB].filter(Boolean).map((token, index) => (
              <HStack key={token?.coin_type} gap="4px" justify={{ base: 'flex-end', lg: 'space-between' }} w={{ base: '100%', lg: 'auto' }}>
                <SingleCoinImage
                  imageUrl={token?.logo_url}
                  coinType={token ? token?.coin_type : ''}
                  w="20px"
                  h="20px"
                  showTagWidth="10px"
                  showTagHeight="10px"
                />
                <Text color={index === 0 ? 'primary' : 'primary_green'} fontSize="14px" whiteSpace="nowrap">
                  {textEllipses(token?.symbol, 10)}
                  &nbsp;
                  {lockRatio ? '--' : percentMap[index === 0 ? (isReverse ? 'percentB' : 'percentA') : isReverse ? 'percentA' : 'percentB']}%
                </Text>
              </HStack>
            ))}
          </Stack>
        </WithTooltipInfo>
      )}
      {type === 'text' && (
        <HTextLabelBox
          isLoading={false}
          label="Deposit Ratio"
          value={`${percentMap[isReverse ? 'percentB' : 'percentA']}% ${textEllipses(tokenA?.symbol)} / ${percentMap[isReverse ? 'percentA' : 'percentB']}% ${textEllipses(tokenB?.symbol)} `}
          labelStyle={{
            fontSize: '14px'
          }}
          valueStyle={{
            fontSize: '14px',
            maxW: 'unset'
          }}
          skeletonStyle={{
            valueW: '128px'
          }}
        />
      )}
      {type === 'zap' && (
        <HStack w="100%" justify="space-between" h="16px">
          <Text fontSize="12px" color="text_caption">
            {label || 'Expected Amounts toAdd'}
          </Text>
          {isLoading ? (
            <Skeleton w={{ base: '100px', lg: '150px' }} h="16px" />
          ) : (
            <Stack flexDir={{ base: 'column', lg: 'row' }} gap={{ base: '4px', lg: '12px' }} justify={{ base: 'flex-end', lg: 'space-between' }}>
              {[!isReverse ? tokenA : tokenB, !isReverse ? tokenB : tokenA].filter(Boolean).map((token, index) => (
                <HStack key={token?.coin_type} gap="4px" justify={{ base: 'flex-end', lg: 'space-between' }} w={{ base: '100%', lg: 'auto' }}>
                  {/* <SingleCoinImage
                    imageUrl={token?.logo_url}
                    coinType={token ? token?.coin_type : ''}
                    w="20px"
                    h="20px"
                    showTagWidth="10px"
                    showTagHeight="10px"
                  /> */}
                  <Text color={index === 0 ? 'primary' : 'primary_green'} fontSize="12px" whiteSpace="nowrap">
                    {textEllipses(token?.symbol, 10)}
                    &nbsp;
                    {lockRatio ? '--' : percentMap[index === 0 ? (isReverse ? 'percentB' : 'percentA') : isReverse ? 'percentA' : 'percentB']}%
                  </Text>
                </HStack>
              ))}
            </Stack>
          )}
        </HStack>
      )}
    </>
  )
}

export default DepositRatio
